<template>
  <div>
    <section class="search">
      <h1>Track Player here</h1>
      <form v-on:submit.prevent="onSubmit">
        <div class="form-group">
          <label for="platform">Platform</label>
          <select name="gamertag" v-model="platform" id="gamertag">
            <option value="psn">PS</option>
            <option value="xbl">XBOX</option>
            <option value="origin">Origin</option>
          </select>
        </div>
        <div class="form-group">
          <label for="gamertag">Gamertag</label>
          <input
            type="text"
            v-model="gamertag"
            name="gamertag"
            id="gamertag"
            placeholder="Xbox Live Gamertag, PSN ID, Origin ID, etc"
          />
        </div>
        <div class="form-group">
          <input type="submit" class="btn" value="Check !" />
        </div>
      </form>
    </section>
  </div>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
      platform: "psn",
      gamertag: ""
    };
  },
  beforeCreate() {
    document.body.className = "body-bg-image";
  },
  methods: {
    onSubmit() {
      if (!this.gamertag) {
        this.$toasted.show("Please input the Gamertag", {
          duration: 3000,
          icon: "exclamation-circle"
        });
      } else {
        this.$router.push(`/api/v1/${this.platform}/${this.gamertag}`);
      }
    }
  }
};
</script>

<style></style>
