<template>
  <section id="profile">
    <!-- Loading -->
    <div v-if="loading">
      <h1>Loading...</h1>
    </div>
    <!-- error -->
    <div v-if="error">
      <h1>{{ error }}</h1>
      <router-link tag="div" class="back" to="/">Go back</router-link>
    </div>
    <!-- Get data -->
    <div v-if="profileData">
      <div class="container">
        <div class="profile">
          <div class="gamertag">
            <img :src="profileData.platformInfo.avatarUrl" class="display-avatar" alt />
            <h1>{{ profileData.platformInfo.platformUserId }}</h1>
          </div>
          <div class="grid">
            <div class="img">
              <img :src="profileData.segments[1].metadata.imageUrl" alt />
            </div>
            <div class="stats">
              <ul>
                <li>
                  <h4>Selected legend</h4>
                  <p>{{ profileData.metadata.activeLegendName }}</p>
                </li>
                <li v-if="profileData.segments[0].stats.damage">
                  <h4>{{ profileData.segments[0].stats.damage.displayName }}</h4>
                  <h2>{{ profileData.segments[0].stats.damage.displayValue }}</h2>
                </li>
                <li v-if="profileData.segments[0].stats.headshots">
                  <h4>{{ profileData.segments[0].stats.headshots.displayName }}</h4>
                  <h2>{{ profileData.segments[0].stats.headshots.displayValue }}</h2>
                </li>
                <li v-if="profileData.segments[0].stats.kills">
                  <h4>{{ profileData.segments[0].stats.kills.displayName }}</h4>
                  <h2>{{ profileData.segments[0].stats.kills.displayValue }}</h2>
                </li>
                <li v-if="profileData.segments[0].stats.level">
                  <h4>{{ profileData.segments[0].stats.level.displayName }}</h4>
                  <h2>{{ profileData.segments[0].stats.level.displayValue }}</h2>
                </li>
              </ul>
              <router-link tag="div" class="back" to="/">Go back</router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";
// import backButton from "./backbutton";
export default {
  name: "Profile",
  components: {
    // backButton,
  },
  data() {
    return {
      loading: null,
      error: null,
      profileData: null
    };
  },
  beforeCreate() {
    document.body.className = "body-bg-no-image";
    this.loading = false;
  },
  async created() {
    try {
      this.loading = true;
      const respon = await axios.get(
        `/api/v1/profile/${this.$route.params.platform}/${this.$route.params.gamertag}`
      );
      this.profileData = respon.data.data;
      this.loading = false;
    } catch (err) {
      this.error = err.response.data.messages;
      this.loading = false;
    }
  }
};
</script>

<style scoped>
.container {
  max-width: 750px;
  margin: 2rem auto;
  padding: 0 1rem;
}

.back {
  color: #fff;
  font-weight: 700;
  margin-top: 1rem;
  border: #fff;
  padding: 1rem;
  border-radius: 1.5rem;
  border: 2px solid #fff;
  cursor: pointer;
  width: 10rem;
  text-align: center;
}

.back:hover {
  padding: 1.3rem;
}

.profile {
  display: block;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.6);
  align-items: center;
  box-sizing: border-box;
  padding: 1.5rem 3rem;
  border-radius: 1.5rem;
  text-align: center;
  box-sizing: border-box;
}

.gamertag {
  display: flex;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.7);
  align-items: center;
  box-sizing: border-box;
  padding: 0.5rem 1rem;
  border-radius: 1.5rem;
  width: 100%;
}

.gamertag img {
  width: 4ch;
}

.gamertag h1 {
  margin-left: 1rem;
  font-size: 20px;
}

.grid .stats li {
  background-color: rgba(0, 0, 0, 0.7);
  padding: 1rem;
  text-align: left;
  font-size: 100%;
  border-radius: 1.5rem;
  margin-top: 1rem;
}

.grid .stats li:hover {
  font-size: 120%;
}

@media (max-width: 699px) {
  .grid .img img {
    width: 60%;
  }
  .grid .stats ul {
    margin-top: 40px;
  }
}

@media (min-width: 700px) {
  .grid {
    display: grid;
    margin-top: 2rem;
    width: 100%;
    grid-template-columns: 1fr 1fr;
  }

  .grid .img {
    grid-column-start: 1 / 2;
  }

  .grid .stats {
    grid-column-start: 2 / 3;
  }
}
</style>
